  margin-top: 3em;
  position: relative;
  -webkit-animation-name: contents;
          animation-name: contents;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}
@-webkit-keyframes contents {
  0% {right: -1000px;}
  60% {right: 50px;}
  70%{right: -50px }
  75%{right: 30px}
  80% {right: -30px;}
  85%{right:20px}
  90%{right: -20px}
  95%{right: 10px}
  100% {right:0}
  
}
@keyframes contents {
  0% {right: -1000px;}
  60% {right: 50px;}
  70%{right: -50px }
  75%{right: 30px}
  80% {right: -30px;}
  85%{right:20px}
  90%{right: -20px}
  95%{right: 10px}
  100% {right:0}
  
}
